@import "../const";

.markdown_page {

  .markdown {
    padding: 40px;
  }
}

.markdown {
  overflow-y: scroll;
  width: 100%;

  &::-webkit-scrollbar {
    display: none;
  }

  .katex-html {
    display: none;
  }

  .katex {
    * {
      user-select: text;
      font-family: "Times New Roman", sans-serif;
    }
  }

  div, span, p, li, ol {
    font-family: "HarmonyOS Sans SC", sans-serif;
    font-weight: 300;
    font-size: 0.96rem;
    line-height: 2rem;
    word-wrap: normal;
    text-wrap: normal;
    user-select: text;
  }

  li {
    ol {
      margin-left: 16px;
    }
  }

  ol {
    li {
      list-style-type: decimal;
      list-style-position: inside;
      padding-left: 5px;

      p {
        display: inline;
      }
    }

  }

  code {
    line-height: 1rem;
    user-select: text;
    font-family: "HarmonyOS Sans SC", sans-serif;
    font-weight: 300;
    font-size: 0.96rem;

    * {
      font-family: "HarmonyOS Sans SC", sans-serif;
      font-weight: 300;
      font-size: 0.96rem;
      line-height: unset;
      user-select: text;
    }
  }

  p .inlineCode {
    background-color: $code_bc;
    color: rgb(56, 58, 66);
    border-radius: 6px;
    padding: 3px 5px;
    margin: 0 2px;
    font-family: J_Light, sans-serif;
    font-weight: lighter;
    font-size: 0.72rem;
  }


  pre {

    div {
      background: $code_bc !important;
      color: rgb(56, 58, 66);
      direction: ltr;
      text-align: left;
      white-space: pre;
      word-spacing: normal;
      word-break: normal;
      tab-size: 2;
      hyphens: none;
      padding: 1em;
      margin: 0.5em 0;
      overflow: auto;
      border-radius: 0.6em;
      border: 1px solid $code_bc;
      transition: all 200ms;

      &:hover {
        background: #f8f8f8 !important;
        //border: 1px solid #d5d5d5;
      }

      span, code {
        font-size: 0.8rem;
        font-family: J_Light, sans-serif;
        font-weight: lighter;

        * {
          line-height: 0.8rem;
        }
      }
    }

    .language-CSS {
      .hljs-number {
        color: #1c8a4d;
      }

      .hljs-selector-class, .hljs-attribute {
        color: #454bdd;
      }

      .hljs-comment {
        color: #8C8C8C;
      }
    }


    .language-Rust {
      font-family: J_Light, sans-serif;
      background: transparent;
      color: rgb(56, 58, 66);
      direction: ltr;
      text-align: left;
      white-space: pre;
      word-spacing: normal;
      word-break: normal;
      tab-size: 2;
      hyphens: none;
      font-weight: lighter;


      .hljs-keyword {
        color: #0033B3;
      }

      .hljs-number {
        color: #1750EB;
      }

      .hljs-title {
        color: #00627A;
      }

      .hljs-built_in {
        color: #d75d0e;
      }

      .hljs-annotation {
        color: #9E880D;
      }

      .hljs-comment {
        color: #8C8C8C;
      }

      .hljs-string {
        color: #067D17;
      }
    }

    .language-cpp {
      @extend .language-Rust
    }


  }

  table {
    width: 100%;
    margin: 10px 0;
    border: $table_border;

    td {
      line-height: 2rem;
      font-size: 0.8rem;
      color: #727272;
      text-align: center;
      font-family: "Noto_Medium", "HarmonyOS Sans SC", "HarmonyOS Sans", "sans-serif";
    }

    tbody {

      tr {
        border-bottom: $table_border;
      }
    }

  }

  a {
    background-color: #ffedd8;
    color: #644017;
    text-decoration: unset;
    border-radius: 4px;
    transition: all 200ms;
    font-size: 0.8rem;
    margin: 0 2px;
    padding: 4px;

    &:hover {
      background-color: #f8e1c8;
      color: #502d04;
    }
  }

  strong {
    user-select: text;
  }

  h1, h2, h3, h4, h5, h6 {
    cursor: pointer;
    width: fit-content;
    box-sizing: border-box;
    padding: 0 10px;
    margin-left: -10px;
    transition: all 200ms;
    border-radius: 20px;
    position: relative;
    overflow: visible;
    user-select: text;

    &:hover {
      &:before {
        content: "";
        position: absolute;
        font-weight: lighter;
        font-family: J_Light, sans-serif;
        color: #777777;
        font-size: 0.8rem;
        left: -20px;
      }
    }
  }

  h1 {
    font-size: 2.4rem;
    font-weight: 600;
    font-family: J_Bold, sans-serif;
    margin: 30px 0 30px;
    padding-bottom: 20px;
    width: 100%;
    border-bottom: $border;
    border-radius: 0;

    &:hover {
      &:before {
        content: "h1";
      }
    }
  }

  h2 {
    font-size: 2rem;
    line-height: 4rem;
    font-family: J_Bold, sans-serif;
    margin: 20px 0;
    font-weight: 600;

    &:hover {
      &:before {
        content: "h2";
      }
    }
  }

  h3 {
    font-size: 2rem;
    line-height: 4rem;
    font-family: J_Bold, sans-serif;

    &:hover {
      &:before {
        content: "h3";
      }
    }
  }

  .info {
    background-color: #dfeefd;
    padding: 20px;
    border-radius: 10px;
    margin: 10px 0;
  }

  .warn {
    @extend .info;
    background-color: #faf0d5;
  }

  .succ {
    @extend .info;
    background-color: #edf8db;
  }

  .dang {
    @extend .info;
    background-color: #fbe6e9;
  }

  .tips {
    @extend .info;
    background-color: #eff0f0;
  }

}

.current {
  background-color: #124dd7;
  color: #ffffff;
  border-radius: 4px;

  &:hover {
    background-color: #1a48ab;
    color: #ffffff;
  }
}

.markdown_page {
  position: relative;
  height: fit-content;
  display: flex;

  .markdown_slide {
    padding: 5px;
    height: 100%;
    width: 300px;
    max-width: 300px;
    min-width: 300px;
    border-right: $border;
    position: sticky;
    top: $header_height;

    * {
      font-size: 0.88rem;
      color: #727272;
      //font-family: Noto_Medium, sans-serif;

    }


    .accordion {
      border-radius: 8px;
      margin-top: 2px;
      border: 1px solid transparent;

      .accordion_btn {
        display: flex;
        justify-content: space-between;
        align-items: center;
      }

      .accordion_body {
        margin: 4px;
      }
    }

    .open {
      border: 1px solid #eeeeee;
    }

    .list {
      line-height: 30px;
      padding: 0 6px;
      border-radius: 8px;
      color: #1e1e1e;

      &:hover {
        background-color: rgba(229, 229, 229, 0.21);
      }
    }


    .section_title {
      font-family: Noto_Medium, sans-serif;
      color: #808080;
      text-transform: uppercase;
      font-size: 0.9rem;
      margin: 30px 0 5px 0;
    }

    .title {
      svg {
        display: inline;
        margin-right: 5px;
      }

      display: flex;
      //justify-content: center;
      align-items: center;

      text-transform: uppercase;
      font-size: 1.2rem;
      color: #494949;
      font-family: Noto_Medium, sans-serif;
      margin: 20px 0 20px;
    }
  }


  .navigation {
    min-width: 250px;
    max-width: 250px;
    position: sticky;
    top: $header_height;
    height: 100%;
    padding: 30px 0;

    .title-anchor {
      box-sizing: border-box;
      padding: 1px 4px;
      margin: 2px 0;
      border-radius: 6px;
    }

    .title-level3 {
      margin-left: 20px;
    }

    .active {
      color: #124dd7;
    }
  }


  .directory_list {
    margin-top: 10px;
    display: grid;
    width: 100%;
    grid-template-columns: 1fr 1fr 1fr;
    justify-content: space-between;
    grid-gap: 10px;

    .content_item {
      height: 140px;
      padding: 10px;
      border-radius: 10px;
      font-family: J_Light, sans-serif;
      transition: all 200ms;
      overflow: hidden;
      position: relative;
      cursor: pointer;


      .name {
        transition: all 200ms;
        font-size: 1.5rem;
        white-space: nowrap;
        font-family: light, sans-serif;
        text-transform: uppercase;
      }

      .info {
        position: absolute;
        bottom: 10px;
        left: 10px;
        font-size: 0.7rem;
      }

      &:hover {
        scale: 0.96;

        box-shadow: 0 0 0 5px rgba(192, 192, 192, 0.5);
        transition: all 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275);

        .name {
        }
      }

      &:active {
        scale: 0.9;
      }
    }
  }

  .articleFilesList {
    width: 100%;
    margin-top: 30px;

    .article_item {
      display: flex;
      align-items: center;
      height: 60px;
      width: 100%;
      border-top: 1px solid #eaeaea;
      padding: 20px;

      &:first-of-type {
        border-top: none;
      }

      &:hover {
        background-color: #f6f6f6;
      }

      .name {
        font-family: light, sans-serif;

      }
    }
  }


}



